import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Link, Button, Div,  platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';

const osname = platform();

const House = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={() => window.history.back()}>
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Пирог		
		</PanelHeader>
<Group>
<List>
<Cell indicator={<Link component = "a" href = "https://www.spletenie.ru/users/olga-babich/povarenok/">Ольга Бабич</Link>}>Автор  </Cell>
<Cell indicator={<Link component = "a" href = "https://www.povarenok.ru">povarenok.ru</Link>}>Ресурс </Cell>

</List>
</Group> 
 <Group>
              <List>
                <Cell indicator="50 минут">Время приготовления</Cell>
             </List>
            </Group>





<Group>
<Cell>Ингредиенты:
<br />• Сметана — 4 ст. л.
<br />• Майонез — 4 ст. л.
<br />• Яйцо куриное — 2 шт
<br />• Мука пшеничная — 9 ст. л.
<br />• Сыр твердый
</Cell>
</Group>

         <Group>
<Cell>Рецепт: 
<br />• Смешать майонез, сметану, яйца
<br />• Добавить муку - масса 
<br /> получается как сметана.
<br />  Разделите на 2 части. 
<br />• Одну часть выложить на 
<br /> сухую сковороду, 
<br />предварительно ее разогрев, 
<br /> огонь уменьшить!
<br />• Далее укладываете продукты, 
<br /> как вам нравится и какие хотите. 
<br /> Сыр должен быть последним слоем. 
<br />• Обильно присыпать сыром, 
<br />накрыть крышкой
<br />• Пирог будет готов, 
<br />как только сыр полностью 
<br /> расплавится.

</Cell>
</Group>


	</Panel>
);

House.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default House;
