import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, HeaderContext, Icon24Search, PanelHeaderContent, ActionSheetItem, ActionSheet, PanelSpinner, Input, Tappable, Select, Textarea, Checkbox, FormLayout, HorizontalScroll,   Search, ScreenSpinner, Link, ListItem, UsersStack, Epic, Div, CellButton, FixedLayout, TabbarItem,  Tabbar,  platform, TabsItem,  Tabs, IOS, Cell, List, View, Group, Footer, Avatar, Button} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Mention from '@vkontakte/icons/dist/24/mention';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon24Add from '@vkontakte/icons/dist/24/add';
import Icon24List from '@vkontakte/icons/dist/24/list';
import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';
import Icon24Filter from '@vkontakte/icons/dist/24/filter';
import Icon28Settings from '@vkontakte/icons/dist/28/settings';
import Icon28Write from '@vkontakte/icons/dist/28/write';
import Icon24Write from '@vkontakte/icons/dist/24/write';
import Icon24Services from '@vkontakte/icons/dist/24/services';
import Icon24Settings from '@vkontakte/icons/dist/24/settings';
import Icon24Info from '@vkontakte/icons/dist/24/info';
import rec from '../img/rec.png';

import Icon16Dropdown from '@vkontakte/icons/dist/16/dropdown';
const osname = platform();
let thematics = [
    {
        "name": "Выпечка",
        "to": "yz"
    },
    {
        "name": "Жаренное",
        "to": "ob"
    },
    {
        "name": "Ужин",
        "to": "zav"
    }, 
    {
        "name": "5 минут",
        "to": "min"
    },
    {
        "name": "Обед",
        "to": "ab"
    },
]

class Es extends React.Component{
    constructor(props){
        super(props)
        this.state = {  
        activeStory: 'home', 
        search: ''
        }
        this.onChange = this.onChange.bind(this);

   }
    onChange (search) { this.setState({ search }); }


    get thematics () {
      const search = this.state.search.toLowerCase();
      return thematics.filter(({name}) => name.toLowerCase().indexOf(search) > -1);
    }
    render(){
        let { go, nickname, fetchedUser, onClick, usersTop,  userAcc, name, updateUserNickname, id} = this.props;
        return(
                <Panel id={id}>
    <PanelHeader
      left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
      </HeaderButton>}
    >
      Категории
    </PanelHeader>



                   <FixedLayout vertical="top">
                    <Search theme="default" value={this.state.search} onChange={this.onChange} />
                    </FixedLayout>

                    

                <Group style={{marginTop: "50px"}}>
                      {this.thematics.length > 0 &&
                        <List>
                          {this.thematics.map((thematic, i) => <Cell key={i} expandable onClick={go} data-to={thematic.to}>{thematic.name}</Cell>)}
                        </List>
                      }
                      
                      </Group>
                                            
<Group>
              <List>
                <Cell expandable indicator="Скоро" >Напитки</Cell>
                <Cell expandable indicator="Скоро" >Десерты</Cell>
              </List>
            </Group>





          <Footer>Категории будут добавляться.</Footer>


  </Panel>
        )
    }
}

Es.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
};

export default Es;